import React from "react";
import Axios from "axios";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

import { countries, CountryListing } from "./country"

import './stats.css'

type YearValue = {
  year: number,
  value: number,
}

type StatsState = {
  time: number,

  globalPop: number,
  selectedCountry: string,

  countryPop: YearValue[],
};

function formatNumber(num: number): string {
  let ns = Array.from(num.toString()).reverse()
  let out = []
  for (let i = 0; i < ns.length; i += 3) {
    out.push(ns.slice(i, i + 3).reverse().join(""))
  }

  return out.reverse().join(",")
}

class Stats extends React.Component<{}, StatsState> {
  interval: NodeJS.Timer | null = null;

  constructor(props: any) {
    super(props);

    this.state = { 
      time: Date.now(),

      globalPop: 7925796554,
      selectedCountry: "United Kingdom",

      countryPop: [],
    };
  }

  componentDidMount() {
    this.interval = setInterval(() => this.tick(), 100);
  }

  componentWillUnmount() {
    clearInterval(this.interval as NodeJS.Timer);
  }

  tick() {
    this.setState(state => ({
      globalPop: state.globalPop + Math.floor(Math.random() * 1.5)
    }));
  }

  // use https://documenter.getpostman.com/view/1134062/T1LJjU52
  onCountrySelected(listing: CountryListing) {
    Axios.post(
      "https://countriesnow.space/api/v0.1/countries/population",
      { "iso3": listing.code }
    ).then(data => {
      console.log(data.data.data.populationCounts)
      this.setState(state => ({
        countryPop: data.data.data.populationCounts
      }));
    })
  }

  render() {
    return <div className="container">
      <div className="column container-selectors">
{/*        <a href="http://www.google.com">World</a>
*/}
        <DropBox name="Country" elements={countries}
                 default={countries[0]}
                 display={(a: CountryListing) => a.name}
                 onSelected={this.onCountrySelected.bind(this)}/>
      </div>
      <div className="column container-statistics">
        <h1>Global Population</h1>
        <h2>{ formatNumber(this.state.globalPop) }</h2>

        <div className="stat-container">
          <div className="stat-column">
            <h1>Country Population</h1>
              <LineChart width={500} height={300} data={this.state.countryPop}
                         margin={{top: 10, left: 30}} style={{textAlign: "center"}}>
                <CartesianGrid strokeDasharray="3 3"/>
                <XAxis dataKey="year"/>
                <YAxis/>
                <Tooltip/>
                <Legend/>
                <Line type="monotone" dataKey="value" stroke="#000000"/>

              </LineChart>
          </div>
          <div className="stat-column">
            Test
          </div>
        </div>
      </div>
    </div>
  }
}

type DropBoxState<T> = {
  selected: T,
};

type DropBoxProps<T> = { name: string, elements: T[], default: T,
                         display: (element: T) => string,
                         onSelected: (element: T) => void }

class DropBox<T> extends React.Component<{}, DropBoxState<T>> {
  props: DropBoxProps<T>
  constructor(props: DropBoxProps<T>) {
    super(props);

    this.props = props

    this.state = {
      selected: props.default,
    }
  }

  selectItem(el: T) {
    this.props.onSelected(el)
    this.setState(state => ({
      selected: el
    }));
  }

  render() {
    return (
      <li className="dropbox-container">
        <b>{this.props.display(this.state.selected)}</b>

        <ul className="dropbox-list">
          {this.props.elements.map((el: T) => 
            <li className="dropbox-item" id={this.props.display(el)}
                onClick={event => this.selectItem(el)}>{this.props.display(el)}</li> 
          )}
        </ul>
      </li>
    )
  }
}

export { Stats };